	module.exports = {"data":{"site":{"siteMetadata":{"title":"Mike Herchel","author":"Mike Herchel"}},"markdownRemark":{"id":"/Users/mherchel/sites/herchel-new/src/pages/2013-05-21-drupalcon-portland/index.md absPath of file >>> MarkdownRemark","html":"<p>It was an amazing experience to present at Drupalcon Portland. In front of a packed room of ~300 people, I gave this presentation that I had previously given at Drupalcamp Atlanta 2012. The main take-away that I want people to learn is that it takes time and effort to make Drupal’s authoring experience awesome.</p>\n<blockquote>\n<p>Awesomizing your Editor’s Back-end Drupal experience - best session i’ve attended today. OR 201 standing room only. #drupalcon</p>\n<p>— Neil McKay (@neilrmckay) May 21, 2013</p>\n</blockquote>\n<p>This presentation covers a lot of real-world techniques and methodologies on how to make Drupal rock for the back end!</p>\n<p>Enjoy!</p>","frontmatter":{"title":"Drupalcon Portland Presentation Notes","date":"May 21, 2013"}}},"pathContext":{"path":"/drupalcon-portland-session-notes/"}}

